<template>
  <v-app>
    <v-img class="mx-auto mt-4" max-width="180" max-height="148" :src="require('@/assets/playcup.png')" />

    <PageTitle title="臺科普類盃" />

    <Divider />

    <ProgressCircular v-if="!news"/>
    <div v-else>
      <SectionTitle title="最新消息" align="center" />
      <CardText
        v-for="newsData in news" :key="newsData.id"
        :imageIcon="'mdi-newspaper-variant'"
        :cardTitle="newsData.gsx$標題.$t"
        :cardMainText="newsData.gsx$內文.$t"
        :cardSubText="newsData.gsx$日期.$t"
      />
    </div>

    <ProgressCircular v-if="!links"/>
    <div v-else>
      <SectionTitle title="常用連結" align="center" />
      <CardLink
        v-for="link in links" :key="link.id"
        :imageIcon="link.gsx$圖標.$t"
        :cardLink="link.gsx$連結網址.$t"
        :cardTitle="link.gsx$連結名稱.$t"
      />
    </div>

  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';

export default {
  name: 'Home',

  data () {
    return {
      news: null,
      links: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.news = (await googleSheets.get('最新消息')).reverse();
      this.links = (await googleSheets.get('常用連結'));
    }
  }
};
</script>
