<template>
  <v-app>
    <PageTitle title="協同合作" />
    <ProgressCircular v-if="!cooperations"/>
    <div v-else v-for="cooperation in cooperations" :key="cooperation.id">
      <SectionTitle v-if="cooperation.gsx$權重.$t === '1'" class="mx-8" :title="cooperation.gsx$內文.$t" align="left" :textColor="color.text.blue" />
      <ContentText v-if="cooperation.gsx$權重.$t === '2'" class="mx-12" :content="cooperation.gsx$內文.$t" align="left" :blod="true" :textColor="color.text.orange" />
      <ContentText v-if="cooperation.gsx$權重.$t === '3'" class="mx-16" :content="cooperation.gsx$內文.$t" align="left" :blod="false" />
      <v-img v-if="cooperation.gsx$權重.$t === 'image'" class="mx-16" :max-width="width" :src="require(`@/assets/cooperation/${cooperation.gsx$內文.$t}.png`)" />
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';
import color from '@/libraries/color';

export default {
  name: 'Cooperation',

  data () {
    return {
      color: color,
      cooperations: null
    };
  },

  beforeMount () {
    this.init();
  },

  computed: {
    width () {
      let width = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          width = '40%';
          break;
        case 'sm':
          width = '40%';
          break;
        case 'md':
          width = '20%';
          break;
        case 'lg':
          width = '20%';
          break;
        case 'xl':
          width = '20%';
          break;
      }

      return width;
    }
  },

  methods: {
    init: async function () {
      this.cooperations = (await googleSheets.get('協同合作'));
    }
  }
};
</script>
