<template>
  <v-app>
    <PageTitle title="商品專區" />
    <ProgressCircular v-if="!storeDatas"/>
    <div v-else >
      <ContentText
        :content="storeDatas[0].gsx$商品時程.$t"
        align="center"
        :blod="false"
      />

      <v-row class="mx-auto ma-8">
        <v-col v-for="data in storeDatas" :key="data.id">
          <Card
            :image="`store/${data.gsx$id.$t}`"
            :cardTitle="data.gsx$品項.$t"
            :cardInfo="
              data.gsx$品項.$t !== '匯款資訊上傳' ? [
                { type: 'divider' },
                { type: 'button', text: storeDatas[0].gsx$商品狀態.$t, icon: 'mdi-login', disable: storeDatas[0].gsx$商品狀態.$t !== '立即購買' ? true : false, link: data.gsx$報名連結.$t },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: storeDatas[0].gsx$商品狀態.$t === '尚未開放' ? true : false, link: data.gsx$查詢連結.$t }
              ] : [
                { type: 'divider' },
                { type: 'button', text: storeDatas[0].gsx$商品狀態.$t === '尚未開放' ? storeDatas[0].gsx$商品狀態.$t : '立即上傳', icon: 'mdi-upload', disable: storeDatas[0].gsx$商品狀態.$t === '尚未開放' ? true : false, link: data.gsx$報名連結.$t },
              ]
            "/>
        </v-col>
      </v-row>
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';

export default {
  name: 'Store',

  data () {
    return {
      storeDatas: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.storeDatas = (await googleSheets.get('商品專區'));
    }
  }
};
</script>
