<template>
  <v-app>
    <PageTitle :title="typeMap[type]" />
    <ProgressCircular v-if="!qas"/>
    <div v-else>
      <CardText
        v-for="qa in qas" :key="qa.id"
        :imageIcon="'mdi-help-box'"
        :cardTitle="qa.gsx$問題.$t"
        :cardMainText="qa.gsx$解答.$t"
        cardSubText=""
      />
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';

export default {
  name: 'Qa',

  data () {
    return {
      typeMap: {
        signup: '關於報名',
        competition: '關於比賽',
        store: '關於商品'
      },
      qas: null
    };
  },

  props: {
    type: {
      type: String,
      required: true
    }
  },

  watch: {
    type: function () {
      this.init();
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.qas = null;
      const allQas = (await googleSheets.get('常見問答'));
      const qas = [];
      for (const qa of allQas) {
        if (qa.gsx$種類.$t === this.type) {
          qas.push(qa);
        }
      }
      this.qas = qas;
    }
  }
};
</script>
