import axios from 'axios';

const SHEET_ID = '1LgVtMUZhIQ6d73DVKpSQ4SfFLvObAlitaOzNGatnmSY';
const GID_MAP = {
  最新消息: 1,
  常用連結: 2,
  歷史沿革介紹: 3,
  歷史沿革事件: 4,
  競賽章程: 5,
  線上報名: 6,
  商品專區: 7,
  賽事公告: 8,
  常見問答: 9,
  主辦單位: 10,
  協同合作: 11
};

const get = function (sheetName) {
  return new Promise((resolve) => {
    axios
      .get(`https://spreadsheets.google.com/feeds/list/${SHEET_ID}/${GID_MAP[sheetName]}/public/values?alt=json`)
      .then((response) => {
        const gidTitleCheck = response.data.feed.title.$t === sheetName;
        resolve(gidTitleCheck ? cleanData(response.data.feed.entry) : null);
      });
  });
};

const cleanData = function (entries) {
  for (const entry of entries) {
    delete entry.category;
    delete entry.content;
    delete entry.id;
    delete entry.link;
    delete entry.title;
    delete entry.updated;
  }

  return entries;
};

export default {
  get
};
