<template>
  <v-app>
    <PageTitle title="競賽章程" />
    <ProgressCircular v-if="!charters"/>
    <div v-else v-for="charter in charters" :key="charter.id">
      <SectionTitle v-if="charter.gsx$權重.$t === '1'" :class="margin(8)" :title="charter.gsx$內文.$t" align="left" :textColor="color.text.blue" />
      <ContentText v-if="charter.gsx$權重.$t === '2'" :class="margin(12)" :content="charter.gsx$內文.$t" align="left" :blod="true" :textColor="color.text.orange" />
      <ContentText v-if="charter.gsx$權重.$t === '3'" :class="margin(16)" :content="charter.gsx$內文.$t" align="left" :blod="false" />
      <ContentText v-if="charter.gsx$權重.$t === 'link'" :class="margin(16)" :content="charter.gsx$內文.$t" align="left" :blod="false" :link="charter.gsx$連結.$t"/>
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';
import color from '@/libraries/color';

export default {
  name: 'Charter',

  data () {
    return {
      color: color,
      charters: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.charters = (await googleSheets.get('競賽章程'));
    },
    margin (px) {
      let margin = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          margin = `mx-${px - 4}`;
          break;
        case 'sm':
          margin = `mx-${px - 4}`;
          break;
        case 'md':
          margin = `mx-${px}`;
          break;
        case 'lg':
          margin = `mx-${px}`;
          break;
        case 'xl':
          margin = `mx-${px}`;
          break;
      }

      return margin;
    }
  }
};
</script>
