<template>
  <v-app>
    <PageTitle title="線上報名" />
    <ProgressCircular v-if="!signupDatas"/>
    <div v-else >
      <ContentText
        :content="signupDatas[0].gsx$報名時程.$t"
        align="center"
        :blod="false"
      />

      <v-row class="mx-auto ma-8">
        <v-col v-for="data in signupDatas" :key="data.id">
          <Card
            :image="`signup/${data.gsx$id.$t}`"
            :cardTitle="data.gsx$組別.$t"
            :cardInfo="
              data.gsx$組別.$t !== '照片上傳' ? [
                { type: 'divider' },
                { type: 'button', text: signupDatas[0].gsx$報名狀態.$t, icon: 'mdi-login', disable: signupDatas[0].gsx$報名狀態.$t !== '立即報名' ? true : false, link: data.gsx$報名連結.$t },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: signupDatas[0].gsx$報名狀態.$t === '尚未開放' ? true : false, link: data.gsx$查詢連結.$t }
              ] : [
                { type: 'divider' },
                { type: 'button', text: signupDatas[0].gsx$報名狀態.$t === '尚未開放' ? signupDatas[0].gsx$報名狀態.$t : '立即上傳', icon: 'mdi-upload', disable: signupDatas[0].gsx$報名狀態.$t === '尚未開放' ? true : false, link: data.gsx$報名連結.$t },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: signupDatas[0].gsx$報名狀態.$t === '尚未開放' ? true : false, link: data.gsx$查詢連結.$t }
              ]
          "/>
        </v-col>
      </v-row>
    </div>
  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';

export default {
  name: 'Signup',

  data () {
    return {
      signupDatas: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.signupDatas = (await googleSheets.get('線上報名'));
    }
  }
};
</script>
