<template>
  <v-app>
    <PageTitle title="歷史沿革" />
    <ProgressCircular v-if="!intros"/>
    <div v-else>
      <ContentText
        v-for="intro in intros" :key="intro.id"
        :content="intro.gsx$介紹.$t"
        align="center"
        :blod="false"
      />
    </div>

    <ProgressCircular v-if="!events"/>
    <v-timeline v-else class="ma-4" :dense="dense">
      <v-timeline-item v-for="event in events" :key="event.id" :color="color.panel.blue" icon="mdi-volleyball" large>
        <v-card>
          <v-card-title class="justify-center align-center">
            <v-icon size="24" left :color="color.panel.blue">mdi-calendar</v-icon>
            <div class="text-h6">{{ event.gsx$日期.$t }}</div>
          </v-card-title>

          <v-card-title class="justify-center align-center">{{ event.gsx$標題.$t }}</v-card-title>

          <v-img
            class="mx-auto ma-4"
            :max-width="event.gsx$圖片.$t === 'facebook' ? 120 : 220"
            :src="require(`@/assets/history/${event.gsx$圖片.$t}.png`)"
          />

          <v-card-text class="text-center">
            <div class="ma-2" v-for="line in event.gsx$內文.$t.split('\n')" :key="line.id">{{ line }}<br></div>
          </v-card-text>
        </v-card>

      </v-timeline-item>
    </v-timeline>

  </v-app>
</template>

<script>
import googleSheets from '@/libraries/googleSheets';
import color from '@/libraries/color';

export default {
  name: 'History',

  data () {
    return {
      color: color,
      intros: null,
      events: null
    };
  },

  computed: {
    dense () {
      let dense = false;

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          dense = true;
          break;
        case 'sm':
          dense = true;
          break;
        case 'md':
          dense = false;
          break;
        case 'lg':
          dense = false;
          break;
        case 'xl':
          dense = false;
          break;
      }

      return dense;
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.intros = (await googleSheets.get('歷史沿革介紹'));
      this.events = (await googleSheets.get('歷史沿革事件'));
    }
  }
};
</script>
